<template>
  <div class="g-carousel">
    <el-carousel height="150px" :interval="4000">
      <el-carousel-item v-for="item in imagesbox" :key="item.id" align="center">
        <img v-lazy="item.idview" height="100%" width="100%" />
      </el-carousel-item>
    </el-carousel>
  </div>
</template>
<script>
export default {
  name: "Carousel",
  data() {
    return {
      imagesbox: [
        { id: 0, idview: require("../../assets/img/Carousel/1.png") },
        { id: 1, idview: require("../../assets/img/Carousel/2.png") },
        { id: 2, idview: require("../../assets/img/Carousel/3.png") }
      ]
    };
  }
};
</script>
<style>
.el-carousel {
  margin-top: 3%;
}
.el-carousel-item {
  border-radius: 30px;
}
</style>



